#timerWrapper,
#notesWrapper,
#clockWrapper {
    text-align: center;
    width: 100%;
}
.timer,
#clockWrapper {
    background: black;
    color: white;
    font-size: 1.8em;
    -webkit-box-shadow: 5px 5px 25px rgb(155, 155, 155);
            box-shadow: 5px 5px 25px rgb(155, 155, 155);
    padding: 5px 20px 5px 20px;
    text-align: center;
}

@-webkit-keyframes shiftBackground {
    from {background-color: rgb(202, 0, 0);}
    to {background-color: rgb(255, 221, 221);}
}

@keyframes shiftBackground {
    from {background-color: rgb(202, 0, 0);}
    to {background-color: rgb(255, 221, 221);}
}
.--red {
    background: red;
    -webkit-animation: shiftBackground infinite 2s ease-in-out 1s alternate;
            animation: shiftBackground infinite 2s ease-in-out 1s alternate; 
    -webkit-animation-duration: 2; 
            animation-duration: 2;
    color: black; 
    -webkit-animation-delay: 1s; 
            animation-delay: 1s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}


